import 'notyf/notyf.min.css';
import React, { Suspense, useContext } from 'react';
import { Helmet } from 'react-helmet';
// import { Online, Offline } from 'react-detect-offline'
import {
  BrowserRouter as Router,
  Redirect,
  Route,
  Switch,
} from 'react-router-dom';
import Breakpoint from './components/breakpoint/breakpoint';
import BottomNavbar from './components/Toolbar/bottomNav';
import Navbar from './components/Toolbar/toolbar';
import AuthContextProvider, { AuthContext } from './contexts/auth_context';
import StorageContextProvider from './contexts/storage_context';
import ThemeContextProvider from './contexts/theme_context';
// import Navbar from "./components/navbar"
// import logo from "./logo.svg"
import { ReactComponent as Loader } from './images/loader.svg';
// import Loader from "./components/loader"
import Home from './pages/home';
import withSplashScreen from './pages/splashScreen';
import './themes/scss/App.scss';
import './util.css';

// import Dashboard from "./pages/userDashboard"
// import Settings from "./pages/settings"
// import Login from "./pages/login"
// import Register from "./pages/register"
// import Status from "./pages/status"
// import Page404 from "./pages/404.js"
// import Read from "./pages/readArticles"
// import NewArticle from "./pages/newArticle"
// import EditArticle from "./pages/editArticle"

// const Home = React.lazy(() => import('./pages/home'));
const Dashboard = React.lazy(() => import('./pages/userDashboard'));
const Settings = React.lazy(() => import('./pages/settings'));
const Login = React.lazy(() => import('./pages/login'));
const Register = React.lazy(() => import('./pages/register'));
const Status = React.lazy(() => import('./pages/status'));
const Page404 = React.lazy(() => import('./pages/404.js'));
const Read = React.lazy(() => import('./pages/readArticles'));
const NewArticle = React.lazy(() => import('./pages/newArticle'));
const EditArticle = React.lazy(() => import('./pages/editArticle'));
const CodeOfConduct = React.lazy(() => import('./pages/codeOfConduct'));
const Search = React.lazy(() => import('./pages/search'));
const VerifyEmail = React.lazy(() => import('./pages/verify'));

const suspenseLayout = (children, loader = false) => {
  return (
    <>
      <Suspense
        fallback={
          <>
            {(() => {
              if (loader) {
                return (
                  <>
                    <div
                      className="w-100"
                      style={{
                        display: 'grid',
                        placeItems: 'center',
                        height: '60vh',
                      }}
                    >
                      <Loader className="loader" />
                    </div>
                  </>
                );
              }
            })()}
          </>
        }
      >
        {children}
      </Suspense>
    </>
  );
};
const userDash = (props) => {
  function pageType(p) {
    let t = 0;
    if (p !== null && p !== '') {
      if (p.length > 1 && p.charAt(0) === '@') {
        t = 1;
      } else {
        t = 2;
      }
    }
    return t;
  }

  if (pageType(props.match.params.username) === 1) {
    let u = props.match.params.username;
    u = u.replace('@', '');
    return (
      <>
        <div className="m-top" />
        {suspenseLayout(<Dashboard username={u} />)}
      </>
    );
  }
  return (
    <>
      <div className="m-top" />
      {suspenseLayout(<Page404 />)}
    </>
  );
};

function AppInnerContainer() {
  const { isLoggedIn, user_email_verified } = useContext(AuthContext);

  return (
    <>
      {/* <Online> */}
      <Helmet>
        <meta charSet="utf-8" />
        <title>Devorzo - Your awesome dev community</title>
        <link rel="canonical" href="https://devorzo.github.io/" />
      </Helmet>

      <ThemeContextProvider>
        <Router>
          <div>
            {/* <Breakpoint xs sm md lg xl> */}
            <Navbar />
            {/* </Breakpoint> */}

            <Breakpoint xs sm>
              <div className="fixed-bottom">
                <BottomNavbar />
              </div>
            </Breakpoint>

            {/* <br/> */}
            <Switch>
              <Route exact path="/">
                <div className="m-top" />
                {suspenseLayout(<Home />)}
                {isLoggedIn && user_email_verified !== 1 && (
                  <Redirect to="/verify" />
                )}
              </Route>

              <Route path="/about">
                {/* <div>About</div> */}
                <div className="m-top" />
                {suspenseLayout(<Status />)}
              </Route>
              <Route path="/dashboard">
                <div className="m-top" />
                {isLoggedIn && <>{suspenseLayout(<Dashboard mode="self" />)}</>}
                {!isLoggedIn && (
                  <>
                    <Redirect to="/login" />{' '}
                  </>
                )}
                {isLoggedIn && user_email_verified !== 1 && (
                  <Redirect to="/verify" />
                )}
              </Route>

              <Route path="/settings">
                <div className="m-top" />
                {isLoggedIn && <>{suspenseLayout(<Settings />)}</>}
                {!isLoggedIn && (
                  <>
                    <Redirect to="/login" />{' '}
                  </>
                )}
                {isLoggedIn && user_email_verified !== 1 && (
                  <Redirect to="/verify" />
                )}
              </Route>

              <Route path="/login">
                <div className="m-top" />
                {suspenseLayout(<Login />)}
                {isLoggedIn && <Redirect to="/" />}
              </Route>

              <Route path="/register">
                <div className="m-top" />
                {suspenseLayout(<Register />)}
                {isLoggedIn && <Redirect to="/" />}
              </Route>

              <Route path="/verify">
                <div className="m-top" />
                {suspenseLayout(<VerifyEmail />)}
                {!isLoggedIn && <Redirect to="/" />}
              </Route>

              <Route
                path="/search"
                component={(props) => (
                  <>
                    <div className="m-top" />
                    {suspenseLayout(<Search data={props} />)}
                  </>
                )}
              />

              <Route
                path="/new"
                component={() => (
                  <>
                    <div className="m-top" />
                    {isLoggedIn && user_email_verified !== 1 && (
                      <Redirect to="/verify" />
                    )}
                    {suspenseLayout(<NewArticle />)}
                    {!isLoggedIn && <Redirect to="/login" />}
                  </>
                )}
              />

              <Route
                path="/reset/:c1/:c2/:c3"
                component={(props) => (
                  <>
                    <div className="m-top" />
                    {props.match.params.c1} -{props.match.params.c2} -
                    {props.match.params.c3}
                  </>
                )}
              />
              <Route
                path="/edit/:articleId"
                component={(props) => (
                  <>
                    <div className="m-top" />
                    {!isLoggedIn && <Redirect to="/login" />}
                    {isLoggedIn && user_email_verified !== 1 && (
                      <Redirect to="/verify" />
                    )}
                    {suspenseLayout(
                      <EditArticle articleId={props.match.params.articleId} />,
                    )}
                  </>
                )}
              />

              <Route
                path="/read/:articleId"
                component={(props) => (
                  <>
                    <div className="m-top m-touch" />
                    {suspenseLayout(
                      <Read articleId={props.match.params.articleId} />,
                    )}
                    {isLoggedIn && user_email_verified !== 1 && (
                      <Redirect to="/verify" />
                    )}
                  </>
                )}
              />
              <Route
                path="/code-of-conduct"
                component={() => (
                  <>
                    <div className="m-top" />
                    {suspenseLayout(<CodeOfConduct />)}
                  </>
                )}
              />

              <Route path="/:username" component={userDash} />

              <Route path="*">{suspenseLayout(<Page404 />)}</Route>
            </Switch>
          </div>

          <Breakpoint xs sm>
            <br />
            <br />
          </Breakpoint>
        </Router>
      </ThemeContextProvider>
      {/* </Online> */}

      {/* <Offline>
                <div className="container">
                    <div className="splash-screen text-center mt-2">
                        
                        <img src={logo} alt="" style={{
                            width: "300px",
                            height: "300px"
                        }} />
        
                        <h2 className="mt-2">Seems like you are offline!</h2>
                    </div>
                </div>
            </Offline> */}
    </>
  );
}

const App = () => {
  return (
    <>
      <StorageContextProvider>
        <AuthContextProvider>
          <AppInnerContainer />
        </AuthContextProvider>
      </StorageContextProvider>
    </>
  );
};

export default withSplashScreen(App);
// export default App;
