import React, { useState, useEffect, useCallback } from 'react';

const Breakpoint = (props) => {
  const [show, setShow] = useState(false);
  const [width, setWidth] = useState(window.innerWidth);

  const handleChange = useCallback(() => {
    let c_show = false;
    if (width < 576) {
      if (props.xs === true) {
        c_show = true;
      }
    } else if (width >= 576 && width < 768) {
      if (props.sm === true) {
        c_show = true;
      }
    } else if (width >= 768 && width < 992) {
      if (props.md === true) {
        c_show = true;
      }
    } else if (width >= 992 && width < 1200) {
      if (props.lg === true) {
        c_show = true;
      }
    } else if (width >= 1200) {
      if (props.xl === true) {
        c_show = true;
      }
    }

    if (props.showAll) c_show = true;

    if (props.hide) c_show = false;

    // console.log("changing show: ", c_show)
    // console.log("changed width: ", width)
    setShow(c_show);
  }, [
    width,
    props.xs,
    props.sm,
    props.md,
    props.lg,
    props.xl,
    props.showAll,
    props.hide,
  ]);
  useEffect(() => {
    const handleResize = () => {
      setWidth(window.innerWidth);
    };
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  useEffect(() => {
    handleChange();
  }, [handleChange]);

  return <>{show ? props.children : <></>}</>;
};

export default Breakpoint;
