import React, { Component, createContext } from 'react';
import SecureStorage from '../lib/secureStorage.lib';
import { generateNewKeys } from '../lib/crypto.lib';

export const StorageContext = createContext();

class StorageContextProvider extends Component {
  constructor(props) {
    super(props);

    const k = generateNewKeys();
    const keys = {
      private_key: k.privateKey,
      public_key: k.publicKey,
    };

    this.state = {
      keys,
    };

    console.log(this.state.keys, keys);
    this.randomizeKeys = this.randomizeKeys.bind(this);
    this.setKeys = this.setKeys.bind(this);
    this.getItem = this.getItem.bind(this);
    this.setItem = this.setItem.bind(this);
    this.getStorage = this.getStorage.bind(this);
    this.clearStorage = this.clearStorage.bind(this);
  }

  setKeys(keys) {
    this.setState({
      keys: {
        public_key: keys.public_key,
        private_key: keys.private_key,
      },
    });
  }

  getItem(index_key) {
    return SecureStorage.getItem(index_key, this.state.keys);
  }

  setItem(index_key, data) {
    return SecureStorage.setItem(index_key, data, this.state.keys);
  }

  getStorage() {
    return SecureStorage.getStorage(this.state.keys);
  }

  randomizeKeys() {
    const keys = generateNewKeys();
    this.setState({
      keys: {
        public_key: keys.public_key,
        private_key: keys.private_key,
      },
    });

    return this.state.keys;
  }

  clearStorage() {
    SecureStorage.clear(this.state.keys);
  }

  render() {
    return (
      <StorageContext.Provider
        value={{
          ...this.state,
          setKeys: this.setKeys,
          randomizeKeys: this.randomizeKeys,
          getItem: this.getItem,
          setItem: this.setItem,
          getStorage: this.getStorage,
          clearStorage: this.clearStorage,
        }}
      >
        {this.props.children}
      </StorageContext.Provider>
    );
  }
}

export default StorageContextProvider;
