import React, { useContext, useState, useEffect } from 'react';
import { Link, NavLink, useHistory, useLocation } from 'react-router-dom';
import {
  Close,
  WbSunnyOutlined,
  MenuRounded,
  AccountCircle,
  InfoRounded,
  ArrowDropDownCircleRounded,
  // ArrowDropDownRounded,
  ExitToAppRounded,
  SettingsRounded,
  Brightness2Rounded,
  Brightness2Outlined,
  SearchRounded,
} from '@material-ui/icons';
import { ThemeContext } from '../../contexts/theme_context';
import { AuthContext } from '../../contexts/auth_context';
import DropdownMenu, { DropdownItem } from '../dropdown';
import Breakpoint from '../breakpoint/breakpoint';
import { ReactComponent as Logo } from '../../logo-t.svg';
import { ReactComponent as LogoLight } from '../../logo-light.svg';
import * as serviceWorker from '../../serviceWorker';
// import { ReactComponent as Notification } from '../../icons/notification.svg';
// import { ReactComponent as NotificationActive } from '../../icons/notification-active.svg';

const ThemeToggler = (props) => {
  const { toggleTheme, isLightTheme } = useContext(ThemeContext);

  let f;
  if (isLightTheme === 1) {
    f = <WbSunnyOutlined className="sun-icon" />;
  } else if (isLightTheme === 2) {
    f = <Brightness2Outlined className="moon-icon" />;
  } else {
    f = <Brightness2Rounded className="dark-moon-icon" />;
  }

  const c = `${props.className} nav-link `;
  return (
    <div className={c} onClick={toggleTheme} style={{ width: '1rem' }}>
      {f}
    </div>
  );
};

const NavbarComponent = (props) => {
  const history = useHistory();

  const location = useLocation();

  const [showMobileSearch, setMobileSearch] = useState(false);
  const [Background, setBackground] = useState('light');
  const [Variant, setVariant] = useState('light');
  const [openSideBar, setOpenSideBar] = useState(0);

  const [searchTerm, setSearchTerm] = useState('');

  const { isLightTheme } = useContext(ThemeContext);

  const {
    setAuth,
    removeAuth,
    isLoggedIn,
    user_dp,
    user_fullname,
    user_username,
  } = useContext(AuthContext);

  useEffect(() => {
    if (isLightTheme === 1) {
      // setBackground("light");
      setBackground(null);
      setVariant('light');
    } else {
      setBackground('dark');
      setVariant('dark');
    }
  }, [isLightTheme]);

  useEffect(() => {
    console.log({ location });
    if (location.pathname == '/search') setMobileSearch(true);
    else setMobileSearch(false);
  }, [location]);
  function collapseNav() {
    // let q = document.querySelectorAll("button.navbar-toggler")
    // if (q !== []) {
    //     q[0].click()
    // }
  }

  function handleLogout() {
    removeAuth();
    setOpenSideBar((openSideBar + 1) % 2);
  }

  // function handleLogin() {
  //     setAuth();
  //     setOpenSideBar((openSideBar + 1) % 2);
  // }

  function handleNavOpener() {
    setOpenSideBar((openSideBar + 1) % 2);
    // alert("clicked")
  }

  function resetCache() {
    serviceWorker.unregister();
    window.localStorage.clear();
    window.location.reload();
    handleNavOpener();
  }

  function handleSearchChange(e) {
    e.preventDefault();
    setSearchTerm(e.target.value);
    triggerSearch();
  }

  function triggerSearch() {
    // if (searchTerm !== '') {
    history.push(`/search?q=${searchTerm}`);
    // }
  }

  function onPressEnter(e) {
    if (e.key === 'Enter') {
      // console.log('do validate');
      e.preventDefault();
      triggerSearch();
    }
  }

  const nav_classes = `navbar navbar-expand-md bg-${Background} navbar-${Variant}`;

  const side_nav_classes = `side-nav ${openSideBar === 1 ? 'opened' : ''}`;
  return (
    <>
      <Breakpoint xs sm>
        <div
          className="nav-side-container noselect"
          // style={{ border: '10px solid red' }}
        >
          <div className={side_nav_classes}>
            <div className="contents">
              <div className="content-block">
                {/* <hr className="light"/> */}

                <div className="n-u-container pb-4 pl-4 pr-4 pt-3">
                  {(() => {
                    if (isLoggedIn) {
                      return (
                        <>
                          <div
                            className="m-0 mr-auto p-0"
                            onClick={handleNavOpener}
                          >
                            <Close style={{ fontSize: '40px' }} />
                          </div>

                          <img
                            className="n-avatar-image mt-0 ml-auto mr-auto mb-0"
                            src={user_dp}
                            alt="user"
                          />

                          <span className="n-name">{user_fullname}</span>

                          <span className="n-username">@{user_username}</span>

                          <span className="n-items">
                            <Link
                              className="l-item"
                              onClick={handleNavOpener}
                              to="/"
                            >
                              Home
                            </Link>
                            <Link
                              className="l-item"
                              onClick={handleNavOpener}
                              to="/dashboard"
                            >
                              Dashboard
                            </Link>
                            <Link
                              className="l-item"
                              onClick={handleNavOpener}
                              to="/about"
                            >
                              About
                            </Link>

                            {/* <Link className="l-item" onClick={handleNavOpener} to="/">Bookmarks</Link> */}
                          </span>

                          <span className="n-items n-b-items mt-auto mb-4 pb-4">
                            <a
                              href="#unregister"
                              className="l-item"
                              onClick={resetCache}
                            >
                              Reset App Cache
                            </a>
                            <Link
                              className="l-item"
                              onClick={handleNavOpener}
                              to="/settings"
                            >
                              Settings
                            </Link>
                            <a
                              href="#logout"
                              className="logout"
                              onClick={handleLogout}
                            >
                              Logout
                            </a>
                          </span>
                        </>
                      );
                    }
                    return (
                      <>
                        <span className="n-items">
                          <Link
                            className="l-item"
                            onClick={handleNavOpener}
                            to="/"
                          >
                            Home
                          </Link>
                          <Link
                            className="l-item"
                            onClick={handleNavOpener}
                            to="/about"
                          >
                            About
                          </Link>
                          <Link
                            className="l-item"
                            onClick={handleNavOpener}
                            to="/login"
                          >
                            Login
                          </Link>
                        </span>

                        <span className="n-items n-b-items mt-auto mb-4 pb-4">
                          <a
                            href="#unregister"
                            className="l-item"
                            onClick={resetCache}
                          >
                            Reset App Cache
                          </a>
                        </span>
                      </>
                    );
                  })()}
                </div>
              </div>
            </div>

            <div className="closer" onClick={handleNavOpener} />
          </div>
        </div>
      </Breakpoint>

      <div className="nav-custom-container fixed-top">
        <div className="container">
          <nav className={nav_classes}>
            <Link
              className="navbar-brand ml-auto mr-auto hide-xs hide-sm show-md"
              to="/"
            >
              {(() => {
                if (isLightTheme === 1)
                  return <LogoLight className="nav-logo" />;
                return <Logo className="nav-logo" />;
              })()}
            </Link>

            <Breakpoint hide={showMobileSearch} showAll>
              <span
                className="show-xs show-sm hide-md"
                style={{ width: '60px' }}
              >
                <span
                  className="cs-nav-toggler collapsed"
                  onClick={handleNavOpener}
                >
                  <MenuRounded style={{ fontSize: '30px' }} />
                </span>
              </span>
              <Link
                className="navbar-brand text-center show-xs show-sm hide-md ml-auto mr-auto p-0"
                to="/"
              >
                {(() => {
                  if (isLightTheme === 1)
                    return <LogoLight className="nav-logo" />;
                  return <Logo className="nav-logo" />;
                })()}
              </Link>
              <span
                className="show-xs show-sm hide-md"
                style={{ width: '60px' }}
              >
                <ThemeToggler className="show-xs show-sm hide-md ml-auto p-0" />
              </span>
            </Breakpoint>

            <Breakpoint xs sm hide={!showMobileSearch}>
              <br />
              <form className="form-inline text-center w-100">
                <input
                  className="form-control"
                  type="search"
                  placeholder="Search"
                  aria-label="Search"
                  value={searchTerm}
                  onChange={handleSearchChange}
                  onKeyUp={handleSearchChange}
                  onKeyDown={onPressEnter}
                />

                <SearchRounded
                  className="search-icon"
                  onClick={triggerSearch}
                  style={{
                    cursor: 'pointer',
                  }}
                />
              </form>
            </Breakpoint>

            <div
              className="collapse navbar-collapse"
              id="navbarSupportedContent"
            >
              <form className="form-inline search-box text-center ml-auto">
                <input
                  className="form-control"
                  type="search"
                  placeholder="Search"
                  aria-label="Search"
                  value={searchTerm}
                  onChange={handleSearchChange}
                  onKeyUp={handleSearchChange}
                  onKeyDown={onPressEnter}
                />

                <SearchRounded
                  className="search-icon"
                  onClick={triggerSearch}
                  style={{
                    cursor: 'pointer',
                  }}
                />
              </form>

              <ul className="navbar-nav ml-auto">
                <ThemeToggler className="hide-xs hide-sm show-md mr-4" />
                <NavLink
                  onClick={collapseNav}
                  to="/about"
                  className="nav-link nav-btn"
                >
                  <InfoRounded />
                </NavLink>

                {/* <NavLink
                  onClick={collapseNav}
                  to="/notification"
                  className="nav-link nav-btn"
                >
                  <NotificationActive
                    className="text"
                    style={{ fontSize: '1.5rem' }}
                  />
                </NavLink> */}

                {(() => {
                  if (isLoggedIn) {
                    return (
                      <>
                        <NavLink
                          onClick={collapseNav}
                          to="/dashboard"
                          className="nav-link nav-btn"
                        >
                          <AccountCircle />
                        </NavLink>

                        {/* <Nav.Link href="/newArticle">Edit</Nav.Link> */}

                        {/* <NavLink onClick={collapseNav} to="/newArticle" className="nav-link nav-btn">Create</NavLink> */}

                        {/* <NavDropdown title={<ArrowDropDownRounded />} id="collasible-nav-dropdown" className="nav-btn">

                                                    <NavDropdown.Item onClick={collapseNav} as={Link} to="/settings">Settings</NavDropdown.Item>
                                                    <NavDropdown.Item onClick={collapseNav} as={Link} to="/login">Login</NavDropdown.Item>
                                                    <NavDropdown.Item onClick={collapseNav} as={Link} to="/register">Register</NavDropdown.Item>

                                                    <NavDropdown.Divider />
                                                    <NavDropdown.Item href="#logout" onClick={handleLogout}>Logout</NavDropdown.Item>
                                                </NavDropdown> */}

                        <DropdownMenu
                          title={
                            <ArrowDropDownCircleRounded
                              style={
                                {
                                  // fontSize: "24px"
                                }
                              }
                            />
                          }
                          autoClose
                          addActiveClass
                          className="nav-btn dropdown-toggle nav-link last"
                          containerClassName="nav-btnx dropdown nav-item last"
                        >
                          <DropdownItem
                            type="link"
                            to="/settings"
                            icon={<SettingsRounded />}
                          >
                            Settings
                          </DropdownItem>
                          <hr className="light p-0 m-0" />
                          <DropdownItem
                            className="logout"
                            icon={<ExitToAppRounded />}
                            onClick={handleLogout}
                          >
                            Logout
                          </DropdownItem>
                        </DropdownMenu>
                      </>
                    );
                  }
                  return (
                    <NavLink
                      onClick={collapseNav}
                      to="/login"
                      className="nav-link"
                    >
                      Login
                    </NavLink>
                  );
                })()}
              </ul>
            </div>
          </nav>
        </div>
      </div>
    </>
  );
};

export default NavbarComponent;
