import NaClUtil from 'tweetnacl-util';

NaCl.util = NaClUtil;

export function generateNewKeys() {
  const newKeys = NaCl.box.keyPair();

  // always store ase base64 string
  const publicKey = NaCl.util.encodeBase64(newKeys.publicKey);
  const privateKey = NaCl.util.encodeBase64(newKeys.secretKey);

  return { publicKey, privateKey };
}

export function encryptMessage(
  messageWithoutNonce,
  theirPublicKey,
  mySecretKey,
) {
  const theirPublicKeyUint8Array = NaCl.util.decodeBase64(theirPublicKey);
  const mySecretKeyUint8Array = NaCl.util.decodeBase64(mySecretKey);

  const nonce = newNonce();
  const message = NaCl.util.decodeUTF8(messageWithoutNonce);
  const box = NaCl.box(
    message,
    nonce,
    theirPublicKeyUint8Array,
    mySecretKeyUint8Array,
  );

  const fullMessage = new Uint8Array(nonce.length + box.length);
  fullMessage.set(nonce);
  fullMessage.set(message, nonce.length);

  const base64FullMessage = NaCl.util.encodeBase64(fullMessage);
  return base64FullMessage;
}

export function decryptMessage(messageWithNonce, theirPublicKey, mySecretKey) {
  const theirPublicKeyUint8Array = NaCl.util.decodeBase64(theirPublicKey);
  const mySecretKeyUint8Array = NaCl.util.decodeBase64(mySecretKey);
  const messageWithNonceAsUint8Array = NaCl.util.decodeBase64(messageWithNonce);
  const nonce = messageWithNonceAsUint8Array.slice(0, 24);
  const message = messageWithNonceAsUint8Array.slice(
    24,
    messageWithNonce.length,
  );

  const box = NaCl.box(
    message,
    nonce,
    theirPublicKeyUint8Array,
    mySecretKeyUint8Array,
  );
  const decrypted = NaCl.box.open(
    box,
    nonce,
    theirPublicKeyUint8Array,
    mySecretKeyUint8Array,
  );

  const base64DecryptedMessage = NaCl.util.encodeUTF8(decrypted);
  return base64DecryptedMessage;
}

export function newNonce() {
  // optionally takes a second parameter
  // { type: 'Type' }
  // where 'Type' could be any of
  // - 'Array'
  // - 'Buffer'
  // - 'Uint8Array'
  return NaCl.randomBytes(24);
}

