/* eslint-disable no-control-regex */
import { encryptMessage, decryptMessage } from './crypto.lib';

class SecureStorage {
  static getStorage(security_keys) {
    const { public_key, private_key } = security_keys;
    const storage = window.localStorage.getItem('storage');
    let s = {};
    if (storage === undefined || storage === null || storage === '') {
      return null;
    }
    try {
      // eslint-disable-next-line no-control-regex
      s = JSON.parse(
        decryptMessage(storage, public_key, private_key).replace(/\u0000/g, ''),
      );
      return s;
    } catch (e) {
      console.log(e);
      return null;
    }
  }

  static getItem(index_key, security_keys) {
    const { public_key, private_key } = security_keys;
    const storage = window.localStorage.getItem('storage');
    let s = {};
    if (storage === undefined || storage === null || storage === '') {
      return null;
    }
    try {
      // eslint-disable-next-line no-control-regex
      s = JSON.parse(
        decryptMessage(storage, public_key, private_key).replace(/\u0000/g, ''),
      );
      return s[index_key] ? s[index_key] : null;
    } catch (e) {
      console.log(e);
      return null;
    }
  }

  static setItem(index_key, data, security_keys) {
    const { public_key, private_key } = security_keys;
    const storage = window.localStorage.getItem('storage');
    let s = {};
    if (storage === undefined || storage === null || storage === '') {
      if (index_key !== null && index_key !== undefined && index_key && data) {
        s[index_key] = data;
        s.storage_update = Date.now();
      }
    } else {
      try {
        // eslint-disable-next-line no-control-regex
        s = JSON.parse(
          decryptMessage(storage, public_key, private_key).replace(
            /\u0000/g,
            '',
          ),
        );
        s[index_key] = data;
        s.storage_update = Date.now();
      } catch (e) {
        s[index_key] = data;
        s.storage_update = Date.now();
        console.log(e);
      }
    }
    const enc = encryptMessage(JSON.stringify(s), public_key, private_key);
    window.localStorage.setItem('storage', enc);
  }

  static clear(security_keys) {
    const { public_key, private_key } = security_keys;

    const s = {};

    s.storage_update = Date.now();

    const enc = encryptMessage(JSON.stringify(s), public_key, private_key);
    window.localStorage.setItem('storage', enc);
  }
}

export default SecureStorage;

